#signup {
    position: relative;
    top: 10px;
    text-decoration: none;
}

.login {
    display: block;
    margin: 20px auto; /* 중앙 정렬을 위해 자동 마진 사용 */
    padding: 50px; /* 패딩을 조금 줄여 더 깔끔하게 */
    width: 70%; /* 적당한 폭 설정 */
    border: 2px solid #ccc; /* 점선 대신 실선 사용 */
    border-radius: 10px; /* 모서리를 둥글게 */
    box-shadow: 0 4px 8px rgba(0,0,0,0.1); /* 그림자 효과 추가 */
    position: relative;
    top: 50px; /* 필요에 따라 조정 */
    left: auto;
    background: #f9f9f9; /* 배경색 추가 */
    z-index: 1000;
}
